import React,{Component} from "react";
import {Icon} from "antd";
import {Link,hashHistory} from "react-router";
export default class Login extends Component{
    login(e){
        e.preventDefault();
    }
    sendcheckcode(){

    }
    render(){
        return(
            <div className="loginbox">
                <div className="loginhead">
                    <img src={require("../../../assets/img/testhead.jpg")} alt="error"/>
                    <p><i className="iconfont icon-qq"></i>QQ</p>
                </div>
                <form className="body">
                    <button onClick={this.sendcheckcode}></button>
                    <input autoFocus name="number" type="text" placeholder=""/>
                    <input name="checkcode" type="text" placeholder=""/>
                    <input onTouchStart={this.login} type="submit" value=""/>
                </form>
                <div className="loginfoot">
                    <form>
                        <fieldset>
                            <legend></legend>
                            <div>
                                <span><i className="iconfont icon-qq"></i></span>
                                <span><i className="iconfont icon-weixin"></i></span>
                                <span><i className="iconfont icon-artboard22"></i></span>
                                <span><i className="iconfont icon-xiaomi"></i></span>
                            </div>
                            <p><Link to="/"></Link></p>
                            <button onClick={()=>{hashHistory.push("/")}}></button>
                        </fieldset>
                    </form>
                </div>
            </div>
        )
    }
}


