import React,{Component} from "react";
import {Link,activeClassName} from "react-router";
import {Icon} from 'antd';
import CssTransitionGroup from "react-addons-css-transition-group";
var ifshow = "hide";
export default class More extends Component{
    componentWillUpdate(){
        const {morename} = this.props;
        if(!morename){
            ifshow="show"
        }else{
            ifshow="hide"
        }
    }
    showmore=(e)=>{
        const {togglemore} = this.props;
        if(e.target.tagName!="A"){
            togglemore();
        }
    }
    render(){
        return(
            
            <div onTouchEnd={this.showmore} className={"morebox "+ifshow}>
            <ul className="more">
                <li><Link to="/login"><Icon className="icon" type="user" /></Link></li>
                <li><Link to="/"><Icon className="icon" type="bell" /></Link></li>
                <li><Link to="/"><Icon className="icon" type="cloud-upload" /></Link></li>
                <li><Link to="/"><Icon className="icon" type="camera" /></Link></li>
                <li><Link to="/"><Icon className="icon" type="wifi" />WIFI</Link></li>
                <li><Link to="/"><Icon className="icon" type="exclamation-circle" /></Link></li>
                <li><Link to="/"><Icon className="icon" type="pushpin" /></Link></li>
                <li><Link to="/"><Icon className="icon" type="setting" /></Link></li>
            </ul>
            </div>
        )
    }
}


